import React from "react";
import "./DownHeader.scss";
import Scroll from "react-scroll";
import SponsorshipFile from "../../../files/Sponsorluk_Dosyası.pdf";
const ScrollLink = Scroll.Link;


export default function DownHeader() {
  return (
    <>
      <div className="down-header">
        <section>
          <div className="down-container">
            <h2 className="title-down">We need sponsors</h2>
            <p className="paragraph">
              We need some sponsors to fund us for robotic competitions
            </p>
            <a
              download="true"
              target="_blank"
              rel="noopener noreferrer"
              className="link"
              href={SponsorshipFile}
            >
              Sponsorship file
            </a>
            <ScrollLink
              to="contact-us"
              className="link"
              spy={true}
              smooth={true}
              duration={1000}
              delay={250}
            >
              Let's get in touch
            </ScrollLink>
          </div>
        </section>
      </div>
    </>
  );
}
