import React from "react";
import "./News.scss";
export default function News(props) {
  return (
    <div className="news-wrapper">
      <h2 className="span-title">
        <span className="outline">News</span>
      </h2>
      <ul className="news">
        {props.news.map((data) => {
          return (
            <li
              className="new"
              key={data.Title}
              onClick={() => {
                if (data.Url) {
                  var win = window.open(data.Url, "_blank");
                  win.focus();
                }
              }}
            >
              <div
                className="new-container"
                style={data.Url ? { cursor: "pointer" } : { cursor: "default" }}
              >
                <div className="flex-text">
                  <h2 className="new-pre-title">{data.Title}</h2>
                  <p className="new-text">{data.Text}</p>
                  <img
                    style={{ maxWidth: "2em", height: "auto" }}
                    src={encodeURI(
                      `https://avatars.dicebear.com/api/bottts/${data.Title}.svg`
                    )}
                    alt={`iel robotics ${data.Title}`}
                  />
                  <strong style={{ display: "block" }}>
                    - {data.Author}{" "}
                    <img
                      style={{ maxWidth: "1.5em", height: "auto" }}
                      src={encodeURI(
                        `https://avatars.dicebear.com/api/bottts/${data.Author}.svg`
                      )}
                      alt={`iel robotics ${data.Author}`}
                    />
                  </strong>
                </div>
                <div style={{ margin: "auto", textAlign: "center" }}>
                  {data.Image ? (
                    <img
                      src={data.Image}
                      style={{
                        maxWidth: "10em",
                        height: "auto",
                        borderRadius: "0.5em",
                        marginLeft: "1em",
                      }}
                      loading="lazy"
                      alt={`iel robotics ${data.Title}`}
                    />
                  ) : null}
                </div>
              </div>
            </li>
          );
        })}
      </ul>
    </div>
  );
}
